import { useEffect, useState } from 'react'

import { Link, Outlet, useLocation } from 'react-router-dom'

import {
  Add as AddIcon,
  FitnessCenter,
  CalendarToday,
  Info as InfoIcon,
  InfoOutline as InfoOutlineIcon,
  AutoFixHigh as AutoFixHighIcon,
  FolderOpen as FolderOpenIcon,
} from '@mui/icons-material'
import {
  Container,
  Typography,
  Box,
  Button,
  Card,
  CardContent,
  CardActions,
  Stack,
  Chip,
  CircularProgress,
  Alert,
  LinearProgress,
  IconButton,
} from '@mui/material'

import { usePlanProgress } from '@/hooks/useTrainingExecutions'
import { renderUnit } from '@/utils/unit-utils'

import { getLearningHistoriesByDirectory } from '../../api/learningHistories'
import {
  getTrainingExecutionsByPlanId,
  createTrainingExecution,
  updateTrainingExecution,
} from '../../api/trainingExecutions'
import { getTrainingPlans } from '../../api/trainingPlans'
import { routes } from '../../routes'
import type { TrainingPlan, TrainingExecution } from '../../types/database'
import {
  calculateTotalSessions,
  calculateEndDate,
  formatDate,
  getWeekDayLabel,
} from '../../utils/schedule'

function formatDuration(seconds: number): string {
  if (seconds === 0) return '-'
  const minutes = Math.floor(seconds / 60)
  const secs = seconds % 60
  if (minutes === 0) return `${secs}초`
  return `${minutes}분 ${secs}초`
}

export function PlansPage() {
  const location = useLocation()
  const isListView = location.pathname === '/plans' || location.pathname === '/plans/'

  return (
    <Container maxWidth="md">
      <Box sx={{ my: 4 }}>
        <Box
          sx={{
            display: 'flex',
            justifyContent: 'space-between',
            alignItems: 'center',
            mb: 3,
          }}
        >
          <Box></Box>
          {/* <Typography variant="h4" component="h1">
            훈련 계획
          </Typography> */}
          {isListView && (
            <Button
              component={Link}
              to={routes.plans.new.$()}
              variant="contained"
              startIcon={<AddIcon />}
            >
              새 계획
            </Button>
          )}
        </Box>
        {isListView ? <PlansList /> : <Outlet />}
      </Box>
    </Container>
  )
}

function PlansList() {
  const [plans, setPlans] = useState<TrainingPlan[]>([])
  const [loading, setLoading] = useState(true)
  const [error, setError] = useState<string | null>(null)

  useEffect(() => {
    loadPlans()
  }, [])

  async function loadPlans() {
    try {
      setLoading(true)
      const data = await getTrainingPlans()
      setPlans(data)
    } catch (err) {
      setError('훈련 계획을 불러오는데 실패했습니다.')
      // eslint-disable-next-line no-console
      console.error(err)
    } finally {
      setLoading(false)
    }
  }

  if (loading) {
    return (
      <Box sx={{ display: 'flex', justifyContent: 'center', py: 4 }}>
        <CircularProgress />
      </Box>
    )
  }

  if (error) {
    return <Alert severity="error">{error}</Alert>
  }

  if (plans.length === 0) {
    return <Alert severity="info">아직 훈련 계획이 없습니다. 새 계획을 만들어보세요!</Alert>
  }

  return (
    <Stack spacing={2}>
      {plans.map((plan) => (
        <PlanCard key={plan.id} plan={plan} />
      ))}
    </Stack>
  )
}

function parseFmUrl(unitName: string): { owner: string; repo: string; path?: string } | null {
  if (!unitName.startsWith('fm://')) return null
  const parts = unitName.replace('fm://', '').split('/')
  if (parts.length < 2) return null
  return {
    owner: parts[0],
    repo: parts[1],
    path: parts.length > 2 ? parts.slice(2).join('/') : undefined,
  }
}

function PlanCard({ plan }: { plan: TrainingPlan }) {
  const [generating, setGenerating] = useState(false)
  const [genResult, setGenResult] = useState<string | null>(null)

  const totalSessions = calculateTotalSessions(plan)
  const endDate = calculateEndDate(plan)
  const fmInfo = parseFmUrl(plan.unit_name)

  const { isLoading, data: progress, refetch } = usePlanProgress(plan.id)

  async function handleAutoGenerate() {
    if (!fmInfo) return
    setGenerating(true)
    setGenResult(null)
    try {
      const histories = await getLearningHistoriesByDirectory(
        fmInfo.owner,
        fmInfo.repo,
        fmInfo.path
      )
      if (histories.length === 0) {
        setGenResult('학습 기록이 없습니다.')
        return
      }

      // Group by date (YYYY-MM-DD): count and sum of durations
      const byDate = new Map<string, { count: number; totalDuration: number }>()
      for (const h of histories) {
        const date = h.created_at.slice(0, 10)
        const existing = byDate.get(date) ?? { count: 0, totalDuration: 0 }
        existing.count += 1
        existing.totalDuration += h.duration
        byDate.set(date, existing)
      }

      // Get existing executions for this plan
      const existingExecs = await getTrainingExecutionsByPlanId(plan.id)
      const execByDate = new Map<string, TrainingExecution>()
      for (const e of existingExecs) {
        const date = e.started_at.slice(0, 10)
        execByDate.set(date, e)
      }

      const today = new Date()
      today.setHours(0, 0, 0, 0)
      const twoDaysAgo = new Date(today)
      twoDaysAgo.setDate(twoDaysAgo.getDate() - 2)

      let created = 0
      let updated = 0
      let skipped = 0

      for (const [date, { count: unitCount, totalDuration }] of byDate) {
        const dateObj = new Date(date)
        const durationSeconds = dateObj < twoDaysAgo ? 0 : Math.round(totalDuration / 1000)
        const existing = execByDate.get(date)

        if (existing) {
          if (existing.duration_seconds <= 1) {
            await updateTrainingExecution(existing.id, {
              unit_count: unitCount,
              duration_seconds: durationSeconds,
            })
            updated++
          } else {
            skipped++
          }
          continue
        } else {
          const startOfDay = `${date}T00:00:00.000Z`
          const endOfDay = `${date}T23:59:59.000Z`
          await createTrainingExecution({
            user_id: plan.user_id,
            plan_id: plan.id,
            started_at: startOfDay,
            completed_at: endOfDay,
            duration_seconds: durationSeconds,
            unit_count: unitCount,
          })
          created++
        }
      }

      setGenResult(`생성: ${created}, 업데이트: ${updated}, 스킵: ${skipped}`)
      await refetch()
    } catch (err) {
      // eslint-disable-next-line no-console
      console.error(err)
      setGenResult('자동 생성 중 오류가 발생했습니다.')
    } finally {
      setGenerating(false)
    }
  }

  const progressPercent = progress
    ? Math.min(100, Math.round((progress.completedUnits / plan.total_amount) * 100))
    : 0

  const [showInfo, setShowInfo] = useState(false)
  return (
    <Card>
      <CardContent>
        <Box sx={{ display: 'flex', justifyContent: 'space-between', alignItems: 'center' }}>
          <Box sx={{ display: 'flex', alignItems: 'center', gap: '0.4' }}>
            <CalendarToday sx={{ mr: 1, verticalAlign: 'middle' }} />
            <Typography variant="h6">{plan.name}</Typography>
          </Box>
          <IconButton onClick={() => setShowInfo(!showInfo)}>
            {showInfo ? <InfoIcon /> : <InfoOutlineIcon />}
          </IconButton>
        </Box>
        {showInfo && (
          <>
            <Stack direction="row" flexWrap={'wrap'} spacing={1} sx={{ mb: 1 }}>
              <Chip
                label={`${plan.unit_amount} ${plan.unit_name}/회`}
                size="small"
                variant="outlined"
              />
              <Chip
                label={`총 ${plan.total_amount} ${plan.unit_name}`}
                size="small"
                variant="outlined"
              />
              <Chip
                label={
                  plan.schedule_type === 'daily'
                    ? '매일'
                    : `매주 ${plan.schedule_days?.map(getWeekDayLabel).join(', ')}`
                }
                size="small"
                variant="outlined"
              />
            </Stack>
          </>
        )}

        {/* Progress Section */}
        {isLoading ? (
          <LinearProgress sx={{ my: 1 }} />
        ) : (
          progress && (
            <Box>
              <Box sx={{ display: 'flex', justifyContent: 'space-between', mb: 0.5 }}>
                <Typography variant="body2" color="text.secondary">
                  진행률
                </Typography>
                <Typography variant="body2" fontWeight="medium">
                  {progress.completedUnits} / {plan.total_amount} {renderUnit(plan.unit_name)} (
                  {progressPercent}
                  %)
                </Typography>
              </Box>
              <LinearProgress
                variant="determinate"
                value={progressPercent}
                sx={{ height: 8, borderRadius: 1, mb: 1 }}
              />
              {showInfo && (
                <Stack direction="row" spacing={2} sx={{ mt: 1 }} justifyContent="space-between">
                  <Box>
                    <Typography variant="caption" color="text.secondary">
                      평균 시간: <strong>{formatDuration(progress.avgDurationSeconds)}</strong>•
                      평균 수행량:{' '}
                      <strong>
                        {progress.avgUnitCount} {plan.unit_name}
                      </strong>
                    </Typography>
                  </Box>
                  <Box>
                    <Typography variant="caption" color="text.secondary">
                      수행: <strong>{progress.totalExecutions}회</strong>
                    </Typography>
                  </Box>
                </Stack>
              )}
            </Box>
          )
        )}
        <Box sx={{ display: 'flex', justifyContent: 'space-between' }}>
          <Typography variant="body2" color="text.secondary">
            총 {totalSessions}회
          </Typography>
          <Typography variant="body2" color="text.secondary">
            종료 예정: <strong>{formatDate(endDate)}</strong>
          </Typography>
        </Box>
      </CardContent>
      {genResult && (
        <Box sx={{ px: 2, pb: 1 }}>
          <Typography variant="caption" color="text.secondary">
            {genResult}
          </Typography>
        </Box>
      )}
      <CardActions sx={{ justifyContent: 'space-between' }}>
        <Box>
          <Button component={Link} to={routes.plans.detail.$path({ id: plan.id }, {})} size="small">
            상세보기
          </Button>
          {fmInfo && (
            <Button
              startIcon={generating ? <CircularProgress size={16} /> : <AutoFixHighIcon />}
              size="small"
              onClick={handleAutoGenerate}
              disabled={generating}
            >
              자동생성
            </Button>
          )}
        </Box>
        {fmInfo ? (
          <Button
            startIcon={<FolderOpenIcon />}
            component={Link}
            to={routes.flashMan.contents.$({
              owner: fmInfo.owner,
              repo: fmInfo.repo,
              path: fmInfo.path,
            })}
            size="small"
            variant="contained"
          >
            폴더이동
          </Button>
        ) : (
          <Button
            startIcon={<FitnessCenter />}
            component={Link}
            to={routes.executions.new.$({ planId: plan.id })}
            size="small"
            variant="contained"
          >
            훈련시작
          </Button>
        )}
      </CardActions>
    </Card>
  )
}
